.navigation {
  &__container {
    background: rgb(64, 196, 207);
    display: flex;
    align-items: center;
    &_transperent{
      background:url("http://res.cloudinary.com/ucu/image/upload/v1521105623/Volunteering_Hands_sugvjw.png");
      margin: auto;
      width: 100%;
      height: 500px;
      display: flex;
      align-items: center;
    }
  }

  &__settings{
    margin-left: auto;
    margin-right: 30px;
    &:hover .navigation__settings_name_content {
      display: block;
    }
    &_name{
      cursor: pointer;
      font-size: 14px;
      color: white;
      height: 40px;
      margin-left: 50px;
      //width: 110px;
      border: 0;
      background: transparent;
      text-transform: uppercase;
      font-weight: bold;
      &:focus {outline:0;}
      &:hover{color: #5d89d0;}

      &_content {
        display: none;
        position: absolute;
        background-color: #40c4cf;
        min-width: 150px;
        box-shadow: 0 2px 4px 0 #1696a8;
        z-index: 1;
        font-size: 15px;
        & a {
          padding: 12px 25px;
          text-decoration: none;
          display: block;
          color: white;
          text-align: center;

        }

        & a:hover {
          cursor: pointer;
          color: #5d89d0;

        }
      }
    }
  }


  &__search-bar {
    margin-left: 15px;
    //margin-right: 15px;
    display: inline-block;
    position: relative;
    &:hover .navigation__search-bar_filter_content {
      display: block;
    }

    &_filter {
      cursor: pointer;
      padding-left: 25px;
      font-size: 15px;
      font-weight: 500;
      color: #626977;
      height: 40px;
      width: 275px;
      border-radius: 5px;
      border: 0;
      background: white;
      text-align: left;
       &:focus {
      outline: 0;
    }
      &_content {
        overflow-y: auto;
        overflow-x: hidden;
        max-height: 200px;
        display: none;
        position: absolute;
        background-color: white;
        min-width: 300px;
        box-shadow: 0px 2px 4px 0px #1696a8;
        z-index: 1;
        font-size: 15px;
        & a {
          padding: 12px 25px;
          text-decoration: none;
          display: block;
          color: #393c44;
        }

        & a:hover {
          cursor: pointer;
          background-color: #ddd;
        }
      }
    }
    &_submit {
      cursor: pointer;
      height: 40px;
      width: 50px;
      border-top-right-radius: 5px;
      border-bottom-right-radius: 5px;
      border: 0;
      background: #1696a8;
      &:focus{
        outline: none;
      }

      &-icon {
        background: url('/icons/search.svg');
      }
    }
  }

  &__button {
      border: 2px solid #f5f5f6;
      border-radius: 10px;
      background: #40c4cf;
      min-width: 150px;
      margin-right: 20px;
      color: #f5f5f6;
      font-weight: bold;
      height: 40px;
      cursor: pointer;
    &_right {
      margin-left: auto;
    }
    &:hover {
      border: 2px solid #5d89d0;
      color: #5d89d0;
    }
    &:focus {outline:0;}

  }
  &__logo-content {
    color: #f5f5f6;
    cursor: pointer;
    margin-left: 30px;
    text-decoration: none !important;
    text-transform: uppercase;
  }
  &__profile-photo {;
    height: 30px;
    width: 30px;
    border-radius: 50%;
    vertical-align:middle;
    border: 1.5px solid white;
    &:hover {
      border: 1.5px solid #5d89d0;
    }
  }
  &__subnav {
    background-color: #89d8e0;
    padding-left: 45px;
    padding-right: 45px;
    display: flex;
    justify-content: flex-end;
  }
  &__signUp {
    color: #fafafb;
    font-size: 12px;
    text-transform: uppercase;
    border: none;
    background-color: transparent;
    font-weight: bold;
    line-height: 35px;
    cursor: pointer;

    &:hover {
      color: #40c4cf;
    }
  }
  &__logIn {
    color: #fafafb;
    font-size: 12px;
    text-transform: uppercase;
    border: none;
    background-color: transparent;
    font-weight: bold;
    line-height: 35px;
    cursor: pointer;

    &:hover {
      color: #40c4cf;
    }
  }
  &__divider {
    border-right: 1px solid #fafafb;
    margin: 9px 10px 10px 10px;
    height: 16px;
  }

}
/*Strip the ul of padding and list styling*/
ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  position: absolute;
}

/*Style 'show menu' label button and hide it by default*/
.navigation_show-title {
  text-decoration: none;
  color: #fff;
  background: rgb(64, 196, 207);
  text-align: center;
  padding: 10px 0;
  display: none;
}
/*Hide checkbox*/
input[type='checkbox'] {
  display: none;
}
/*Show menu when invisible checkbox is checked*/
input[type='checkbox']:checked ~ #navigation_container {
  display: block;
}
/*Responsive Styles*/
@media screen and (max-width: 760px) {
  .navigation__container {
    position: static;
    display: none;
  }

  /*Display 'show menu' link*/
  .navigation_show-title {
    display: block;
    width: 100%;
  }
}



